import { annotate, annotationGroup } from 'rough-notation';

const e = document.querySelector('.main-span');
const annotation = annotate(e, { type: 'highlight', strokeWidth: 10, color: '#DADADA', animationDuration: 1500 });
annotation.show();

window.onbeforeunload = function () {
    window.scrollTo(0, 0);
  }